//SEARCH USER FILTERS

export const searchUserFilters = (filter) => {
    return [
      {
        id: 1,
        label: "Condition",
        type: "select",
        disable: filter.operator === "inBetween" ? true : false,
        name: "operator",
        size: "small",
        minWidth: "100wh",
        grid: {
          xs: 12,
          sm: 6,
          md: 4,
          lg: 4,  
        },
        options: [
          // Here we are setup the filter operator items 
          filter.field !== 'date' && { id: 1, name: "Contains", value: "$regex" },
          filter.field !== 'date' && { id: 2, name: "Equals", value: "$eq" },
          filter.field === 'date' && { id: 7, name: "In Between", value: "inBetween" },
  
        ],
      },
      {
        id: 2,
        label: "Select Column",
        type: "select",
        name: "field",
        size: "small",
        minWidth: "100wh",
        grid: {
          xs: 12,
          sm: 6,
          md: 4,  
          lg: 4
        },
       
        options: [
          { id: 1, name: "Name", value: "Name" },
          { id: 2, name: "Username", value: "username" },
          { id: 3, name: "Email", value: "email" },
          { id: 4, name: "Company Name", value: "tenantName" },
          { id: 5, name: "Access Right", value: "tenantRight" },
          { id: 6, name: "Contact", value: "contact" },
          { id: 7, name: "Address", value: "address" },
          { id: 8, name: "Date", value: "date" },
        ]
      },
      {
          id: 3,
          label: "Sorting",
          type: "select",
          name: "sort",
          size: "small",
          options: [
            // Here we are setup the filter operator items 
            { id: 1, name: "Ascending", value: 1 },
            { id: 2, name: "Descending", value: -1 },
          ],
          grid: {
            xs: 12,
            sm: 6,
            md: 4,
            lg: 4 
          },
        },
    ]
  }

//SEARCH USERS INPUTS
export const searchUserInput = (filter) => {
    return [
      filter.field !== "date" && filter.field !== "contact" && {
        id: 1,
        label: `Enter ${filter?.field.replace(/^_/, '') // Remove underscore at the beginning, if any
        .replace(/([A-Z])/g, ' $1') // Split on the basis of uppercase and capitalize each word
        .replace(/^./, function(str){ return str.toUpperCase(); })}`,
        type: "text",
        disabled: filter.field === "" ? true : false,
        name: "searchInput",
        grid: {
          xs: 12,
          sm: 10
        }
      },
      filter.field === "date" && filter.operator === "inBetween" && {
        id: 2,
        label: "Start Date",
        type: "date",
        name: "startDate",
        grid: {
          xs: 12,
          sm: filter.operator === "inBetween" ? 5 : 10
        }
      },
      filter.field === "date" && filter.operator === "inBetween" && {
        id: 3,
        label: "End Date",
        type: "date",
        name: "endDate",
        grid: {
          xs: 12,
          sm: 5
        }
      },
      filter.field === "contact" && {
        id: 4,
        type: "phone",
        grid: {
          xs: 12,
          sm: 8,
          md: 8,
          lg: 10
        }
      },
     {
        id: 5,
        label: "Search",
        type: "button",
        btntype: "submit",
        variant: "contained",
        color: "primary",
        grid: {
          xs: 12,
          sm: 2  
        },
      },
    ]
  }